// app/layout.js или другой основной компонент

import React from 'react';
import { Provider } from 'react-redux';
import { store } from '@/redux/store';
import { RouterProvider } from 'react-router-dom'; // или next/router, если это Next.js

import router from '@/app/router/router'; // Обновите путь, если он отличается

function RootLayout({ children }) {
  return (
    <html lang="en">
      <body>
        <Provider store={store}>
          <RouterProvider router={router} /> {/* или используйте useRouter в Next.js */}
          {children}
        </Provider>
      </body>
    </html>
  );
}

export default RootLayout;
